import { useLocation, useNavigate } from 'react-router-dom';
import 'react-datepicker/dist/react-datepicker.css';
import { useState, useEffect, useRef } from 'react';
import 'assets/css/groupsAddUpdate.css';
import { useDispatch, useSelector } from 'react-redux';
import { showMessage } from 'app/store/fuse/messageSlice';
import { DetailTitleBar } from 'app/theme-layouts/mainLayout/components';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { open, close } from 'app/store/common/loadingWrap';
import { selectComboBox } from 'app/store/common/comboBox';
import { selectGroups, setGroups } from 'app/store/common/groups';

function AddOrUpdate() {
  const dispatch = useDispatch();
  const companyComboBox = useSelector(selectComboBox).company;
  const projectComboBox = useSelector(selectComboBox).project;
  const otaComboBox = useSelector(selectComboBox).otaVersion;
  const [otaCategory, setOtaCategory] = useState([]);
  const [projectCategory, setProjectCategory] = useState([]);
  const [companyCategory, setCompanyCategory] = useState([]);
  const [companyId, setCompanyId] = useState('');
  const [projectId, setProjectId] = useState('');
  const [otaVer, setOtaVer] = useState('');
  const navigate = useNavigate();
  const location = useLocation();
  const param = location.state;
  const nameInput = useRef();
  const groupDesc = useRef();
  const [validation, setValidation] = useState([]);
  const dataList = useSelector(selectGroups).groups;

  const saveBtnClick = () => {
    confirmAlert({
      title: `그룹을 ${param.detailLink[0].title} 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            const groupObj = {};
            groupObj.companyId = companyId;
            groupObj.projectId = projectId;
            groupObj.groupName = nameInput.current.value;
            groupObj.groupDesc = groupDesc.current.value;
            groupObj.otaSeq = otaVer;

            dispatch(open());
            const filter1 = dataList.filter((obj) => obj.projectId === projectId);
            const filter2 = filter1.filter((obj) => obj.groupName === nameInput.current.value);
            if (filter2.length > 0) {
              dispatch(showMessage({ message: '그룹 정보 저장 실패.', variant: 'error' }));
              setValidation([{ type: 'groupName', message: '이미 존재하는 그룹명입니다.' }]);
              dispatch(close());
              return;
            }
            const today = new Date();
            const year = today.getFullYear();
            const month = `0${today.getMonth() + 1}`.slice(-2);
            const day = `0${today.getDate()}`.slice(-2);
            const dateString = `${year}-${month}-${day}`;
            const hours = `0${today.getHours()}`.slice(-2);
            const minutes = `0${today.getMinutes()}`.slice(-2);
            const seconds = `0${today.getSeconds()}`.slice(-2);
            const timeString = `${hours}:${minutes}:${seconds}`;
            const clone = dataList.slice();
            const filter = clone.filter((obj) => obj.groupId !== param.groupId);
            switch (param.detailLink[0].title) {
              case '추가':
                clone.push({
                  companyId,
                  companyName: '(주)한빛',
                  projectId,
                  projectName: projectComboBox.filter((obj) => obj.code === projectId)[0].codeName,
                  groupId: Number(dataList[dataList.length - 1].groupId) + 1,
                  groupName: nameInput.current.value,
                  groupDesc: groupDesc.current.value,
                  deviceCnt: 0,
                  otaSeq: otaVer,
                  otaVer: otaComboBox.filter((obj) => obj.code === otaVer)[0].codeName,
                  updateUsername: '한빛 관리자',
                  updateTime: `${dateString} ${timeString}`,
                });
                dispatch(close());
                dispatch(setGroups({ data: clone }));
                dispatch(
                  showMessage({ message: '그룹 정보가 추가되었습니다.', variant: 'success' })
                );
                navigate('/bss_server/deviceManagement/groups');
                break;
              case '수정':
                filter.push({
                  companyId,
                  companyName: '(주)한빛',
                  projectId,
                  projectName: projectComboBox.filter((obj) => obj.code === projectId)[0].codeName,
                  groupId: param.groupId,
                  groupName: nameInput.current.value,
                  groupDesc: groupDesc.current.value,
                  deviceCnt: 0,
                  otaSeq: otaVer,
                  otaVer: otaComboBox.filter((obj) => obj.code === otaVer)[0].codeName,
                  updateUsername: '한빛 관리자',
                  updateTime: `${dateString} ${timeString}`,
                });
                dispatch(close());
                dispatch(setGroups({ data: filter }));
                dispatch(
                  showMessage({ message: '그룹 정보가 수정되었습니다.', variant: 'success' })
                );
                navigate('/bss_server/deviceManagement/groups');
                break;
              default:
            }
          },
        },
        {
          label: '아니오',
        },
      ],
    });
  };

  const deleteBtnClick = () => {
    confirmAlert({
      title: `그룹 정보를 삭제 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            dispatch(open());
            const clone = dataList.slice();
            const filter = clone.filter((obj) => obj.groupId !== param.groupId);
            dispatch(close());
            dispatch(setGroups({ data: filter }));
            dispatch(showMessage({ message: '그룹 정보가 삭제되었습니다.', variant: 'success' }));
            navigate('/bss_server/deviceManagement/groups');
          },
        },
        {
          label: '취소',
        },
      ],
    });
  };

  useEffect(() => {
    setCompanyCategory(companyComboBox);
    setOtaCategory(otaComboBox);
    if (param.detailLink[0].title === '추가') {
      setCompanyId(companyComboBox[0].code);
      setOtaVer(otaComboBox[0].code);
      const projectArr = projectComboBox.filter((obj) => obj.parentId === companyComboBox[0].code);
      setProjectCategory(projectArr);
      setProjectId(projectArr[0].code);
    } else if (param.detailLink[0].title === '수정') {
      setCompanyId(param.companyId);
      setOtaVer(param.otaSeq);
      setProjectCategory(projectComboBox.filter((obj) => obj.parentId === param.companyId));
      setProjectId(param.projectId);
    }
  }, [param.detailLink[0].title, param.otaSeq, param.companyId, param.projectId]);

  return (
    <div className="section__grid__wrap content groups__add__update">
      <DetailTitleBar
        flag={param.detailLink[0].title}
        deleteBtnClick={deleteBtnClick}
        saveBtnClick={saveBtnClick}
      />
      {/* 그룹 정보 */}
      <section className="group__info">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">그룹정보</p>
          </div>
          <ul>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  회사명
                </dt>
                <dd>
                  <select
                    value={companyId}
                    className="custom__select"
                    onChange={(e) => {
                      setCompanyId(e.target.value);
                      setProjectCategory(
                        projectComboBox.filter((obj) => obj.parentId === e.target.value)
                      );
                      setProjectId(projectCategory[0].code);
                    }}
                  >
                    {companyCategory.map((obj, idx) => (
                      <option key={obj.code} value={obj.code}>
                        {obj.codeName}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  프로젝트명
                </dt>
                <dd>
                  <select
                    value={projectId}
                    className="custom__select"
                    onChange={(evt) => {
                      setProjectId(evt.target.value);
                    }}
                  >
                    {projectCategory.map((obj, idx) => (
                      <option key={obj.code} value={obj.code}>
                        {obj.codeName}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  그룹명
                </dt>
                <dd className="group__name">
                  <input
                    ref={nameInput}
                    type="text"
                    defaultValue={param.groupName}
                    className="custom__input"
                  />
                  {validation.map(
                    (obj) =>
                      obj.type === 'groupName' && (
                        <p className="f__medium field__error">{obj.message}</p>
                      )
                  )}
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>장치수</dt>
                <dd>
                  <input
                    type="number"
                    defaultValue={param.deviceCnt}
                    readOnly
                    className="custom__input readonly"
                  />
                  <button className="custom__btn">
                    <span className="f__medium">보기</span>
                  </button>
                </dd>
              </dl>
            </li>
          </ul>
          <dl>
            <dt>설명</dt>
            <dd>
              <textarea ref={groupDesc} className="custom__input" defaultValue={param.groupDesc} />
            </dd>
          </dl>
        </div>
      </section>
      {/* // 그룹 정보 */}
      {/* OTA정보 */}
      <section className="ota__info">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">OTA 정보</p>
          </div>
          <ul className="list__wrap">
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  적용버전
                </dt>
                <dd>
                  <select
                    className="custom__select"
                    value={otaVer}
                    onChange={(evt) => {
                      setOtaVer(evt.target.value);
                    }}
                  >
                    {otaCategory.map((obj, idx) => (
                      <option key={obj.code} value={obj.code}>
                        {obj.codeName}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>적용일시</dt>
                <dd>
                  <ul>
                    <li>
                      <input
                        value={param.updateTime}
                        className="custom__input readonly"
                        type="text"
                        readOnly
                      />
                    </li>
                  </ul>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>배포이유</dt>
                <dd>
                  <input className="custom__input" defaultValue="" type="text" />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>변경내역</dt>
                <dd>
                  <textarea className="custom__input" defaultValue="" />
                </dd>
              </dl>
            </li>
          </ul>
        </div>
      </section>
      {/* // OTA정보 */}
    </div>
  );
}

export default AddOrUpdate;
